.admin {
  h1 {
    color: white;
    font-size: 4rem;
    font-weight: 500;
    background-color: $grisOscuro;
    border-radius: 5px;
    padding: 0 1rem;
    text-align: center;
    @include tablet {
      text-align: center;
      font-size: 2rem;
    }
  }
}
.alerta {
  text-align: center;
  color: white;
  margin: 0.3rem;
  border-radius: 5px;
  @include tablet {
    margin: 0;
  }
  &.error {
    background-color: rgb(141, 46, 8);
  }
  &.exito {
    background-color: $verde;
  }
}
table.propiedades {
  text-align: center;
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;
  thead {
    background-color: $verde;
    th {
      color: $blanco;
      padding: 0.5rem;
      @include tablet {
        font-size: 1.2rem;
      }
    }
  }
  tbody {
    background-color: $grisOscuro;
    td {
      color: $blanco;
      border: 1px solid white;
      @include tablet {
        font-size: 1rem;
      }
    }
  }
  .imagen-tabla {
    display: block;
    margin: auto;
    width: 12rem;
    @include tablet {
      width: 8rem;
    }
  }
}
