.header {
  background-color: $grisOscuro;
  width: 100%;
  .contenido-header {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: center;
    align-content: stretch;

    a {
      h1 {
        color: $blanco;
        font-weight: $bold;
        @include tablet {
          font-size: 2rem;
          text-align: center;
        }
      }
    }
    .derecha {
      text-align: right;
      color: $blanco;
      background-color: rgba(214, 37, 24, 0.63);
      border-radius: 5px;
      a {
        padding: 10px;
        color: white;
        @include tablet {
          font-size: 1rem;
          padding: 2px;
        }
      }
    }
  }
}
