.titulo-login {
  font-size: 5rem;
  font-weight: 500;
  color: white;
  background-color: $grisOscuro;
  border-radius: 5px;
  @include tablet {
    font-size: 3rem;
  }
}
.contenedor-login {
  border-radius: 10px;
  background-color: $grisOscuro;
  width: 70%;
  height: auto;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  align-content: center;
  padding-bottom: 30px;
  form {
    .boton {
      width: 100%;
    }
    label {
      color: white;
    }
  }
  img {
    width: 250px;
    margin-bottom: 50px;
    @include tablet {
      margin-top: 30px;
      width: 180px;
    }
  }
  a {
    color: rgb(85, 137, 204);
    text-decoration: underline;
  }
}
