.container-inicio {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: flex-start;
  align-items: center;
  align-content: center;
  h1 {
    color: white;
    font-weight: 700;
    @include tablet {
      text-align: center;
    }
  }
  .container-acceder {
    height: 70vh;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: flex-start;
    align-items: center;
    align-content: center;
    a {
      width: 130%;
      text-align: center;
      color: white;
      font-size: 4rem;
      margin-top: 200px;
      border: 2px solid rgb(104, 103, 103);
      background-color: rgb(41, 167, 2);
      border-radius: 5px;
      transition: all 0.2s;
      &:hover {
        color: coral;
        transform: scale(1.2);
      }
    }
  }
}
