.crear {
  h1 {
    color: white;
    font-size: 4rem;
    font-weight: 500;
    background-color: $grisOscuro;
    border-radius: 5px;
    padding: 0 1rem;
    @include tablet {
      font-size: 3rem;
    }
  }
  a {
    border-radius: 5px;
  }
  .contenedor-crear {
    border-radius: 10px;
    background-color: $grisOscuro;
    width: 50%;
    padding: 4rem;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
    align-content: center;
    margin-top: 2rem;
    @include tablet {
      width: 90%;
    }
    label {
      color: white;
    }
    .alerta {
      width: 100%;
    }
  }
}
